body {
  background-color: #FFFFFF;
  position: relative;
  font-family: Open Sans, sans-serif;
}

#back-to-top.show {
  opacity: 1;
}

#back-to-top {
  position: fixed;
  bottom: 0;
  right: 0;
  background: #26A69A;
  padding: 10px;
  margin: 20px;
  transition: opacity 0.2s ease-out;
  opacity: 0;
  z-index: 1;
}

#back-to-top .material-icons {
  color: #FFFFFF;
}

#title-side-nav {
  color: #000000;
  font-weight: bold;
}

h1 {
  font-size: 22px;
}

.spinner-red, .spinner-custom {
  border-color: #26A69A;
}

#container-all {
  -webkit-animation: fadein 1s;
  -moz-animation: fadein 1s;
  -ms-animation: fadein 1s;
  -o-animation: fadein 1s;
  animation: fadein 1s;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-moz-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-ms-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-o-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.container-footer {
  padding-top: 2%;
  padding-bottom: 2%;
}

#loader {
  position: absolute;
  top: 50%;
  left: 50%;
}

#menu:not(.button-collapse-side) {
  width: 100%;
  box-shadow: none;
  background-color: #FFFFFF;
  position: fixed;
  top: 0;
  z-index: 2;
  box-shadow: 0 3px 2px -2px #A5A5A5;
  height: 70px;
}

#nav-mobile {
  position: fixed;
  left: 50%;
  transform: translate(-40%, 0);
  width: 70%;
}

.button-collapse-side {
  display: none;
}

.li-menu a {
  color: #26A69A;
}

.li-menu a:hover {
  background: transparent;
  color: #80cbc4 !important;
}

.li-menu a:after {
  content: '';
  display: block;
  height: 3px;
  width: 0px;
  background: transparent;
  transition: width .5s ease, background-color .5s ease;
  position: relative;
  bottom: 15px;
}

.li-menu a:hover:after {
  width: 100%;
  background: #26A69A;
}

#logo-medpharmacom-menu {
  display: none;
}

#logo-home-pc:hover {
  background: transparent;
}

@media screen and (max-width: 1420px) {
  #nav-mobile {
    width: 85%;
  }
  .li-menu a {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 788px) {
  #nav-mobile {
    width: 100%;
  }
}

@media screen and (min-width: 665px) {
  #logo-medpharmacom-menu-pc {
    width: 100%;
    height: auto;
  }
}

@media screen and (max-width: 660px) {
  #logo-medpharmacom-menu-pc {
    display: none;
  }
  #logo-medpharmacom-menu {
    display: block;
    width: 30%;
  }
  .button-collapse-side {
    position: absolute;
    display: block;
    right: 0;
  }
  #nav-mobile .li-menu:not(#burger-menu) {
    display: none;
  }
  .button-collapse-side {
    top: -40px;
  }
}

@media screen and (max-width: 500px) {
  #logo-medpharmacom-menu {
    width: 40%;
  }
}

@media screen and (max-width: 440px) {
  .button-collapse-side {
    top: -48px;
  }
}

@media screen and (max-width: 380px) {
  #logo-medpharmacom-menu {
    width: 55%;
  }
  .button-collapse-side {
    top: -50px;
  }
}

html {
  height: 100%;
}

#login {
  background-image: url("https://fthmb.tqn.com/s6QHp977r8KcWcVLNqCMUwVbFhI=/5121x3414/filters:fill(auto,1)/nurses-discussing-over-documents-in-hospital-493216353-595690893df78c4eb647536e.jpg");
  background-position: left top;
  background-repeat: no-repeat;
  background-size: cover;
}

#overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
  cursor: pointer;
}

#form-login {
  margin-top: 20%;
  margin-left: auto;
  margin-right: auto;
  width: 400px;
  background: rgba(0, 0, 0, 0.4);
  padding: 2%;
  text-align: center;
  color: #FFFFFF;
  position: relative;
}

@media screen and (max-width: 640px) {
  #forgotten-password {
    float: none;
    margin-right: 5%;
  }
}

@media screen and (max-width: 430px) {
  #form-login {
    width: 100%;
  }
}
